section {
  border-bottom: 1.11111px solid #eaecef;
  width: 640px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 32px;
  font-weight: 600;
  display: block;
}

h2 {
  color: #14a05f;
  font-family: cursive;
}

.notice {
  text-shadow: .1px .3px .2px #757474;
  color: #000;
  opacity: 1;
  font-family: Courier New;
  transition: opacity 1s ease-in-out;
}

.container {
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  width: 640px;
  height: 220px;
  padding: 10px 0;
  display: flex;
  box-shadow: 0 3px 3px -2px #0003, 0 3px 4px #00000024, 0 1px 8px #0000001f;
}

.feedback-buttons {
  gap: 20px;
  display: flex;
}

.feedback-buttons button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 12px;
  transition: box-shadow .15s ease-in;

  &:hover {
    box-shadow: 0 3px 3px -2px #0003, 0 3px 4px #00000024, 0 1px 8px #0000001f;
  }
}

.feedback-buttons img {
  width: 50px;
  height: 50px;
}

.diagram-container {
  opacity: 0;
  align-items: center;
  gap: 20px;
  transition: opacity 1s ease-in-out;
  display: none;
}

.diagram-container.show {
  opacity: 1;
  display: flex;
}

.diagram {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  display: flex;
  position: relative;
  box-shadow: 0 3px 3px -2px #0003, 0 3px 4px #00000024, 0 1px 8px #0000001f;
}

.diagram:before {
  content: "";
  border: 2px solid #020202;
  border-radius: 50%;
  position: absolute;
  inset: 0;
}

.total {
  color: #fff;
  background-color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 75px;
  height: 75px;
  font-size: 27px;
  display: flex;
  position: absolute;
}

.statistic-list {
  width: 250px;
  margin-left: 0;
  padding-left: 0;
  font-family: Arial;
  font-size: 18px;
}

.statistic-list li {
  justify-content: space-between;
  list-style-type: none;
  display: flex;
}

.category {
  font-weight: bold;
}

.dots {
  border-bottom: 1px dotted #000;
  flex-grow: 1;
  margin: 0 4px 2px;
}

.like-icon {
  justify-content: center;
  align-items: center;
  width: 165px;
  height: 165px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.heart-mask {
  width: 100%;
  height: 100%;
  position: absolute;
}

.heart-mask img {
  width: 100%;
  height: 100%;
}

.heart-fill {
  clip-path: url("#heartClipPath");
  transform-origin: bottom;
  z-index: -1;
  background-image: linear-gradient(#00ff6a, #54806f);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 40% 40% 90% 90%;
  width: 87%;
  height: 80%;
  transition: transform .3s;
  position: absolute;
}

.percentage-text {
  color: #000;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
